<template>
    <section>
        <img id="logo" src="@/assets/textlogo.svg">
        <section id="signin" v-bind:class="{ failed: loginFailed, container: true }">
            <article id="body">
                <h2 id="title">Authorize <span class="serviceName">PRAUXY SSL</span></h2>
                <p>Please sign in to your PRAUXY account to allow access.</p>
                <p id="badlogin" v-bind:class="{ showing: loginFailed }">The username or password provided is incorrect.</p>
                <form action="#" method="POST" @submit.prevent="login()">
                    <input v-model="username" name="username" autocomplete="username" id="username" placeholder="Username" required>
                    <label for="username">Username</label>
                    <input v-model="password" name="password" autocomplete="current-password" id="password" type="password" placeholder="Password" required>
                    <label for="password">Password</label>
                    <input type="submit" value="Allow access">
                </form>
                <hr>
                <a href="#">or, create a new account</a>
            </article>
        </section>
    </section>
</template>

<script>
import config from '../config'
export default {
  name: 'Login',
  data: function() {
    return {
      loginFailed: false,
      loginFailReason: null,
      username: "",
      password: ""
    }
  },
  methods: {
      login: function() {
          const data = JSON.stringify({"username":this.username,"password":this.password});

          console.log(data)

          var xhr = new XMLHttpRequest();
          xhr.withCredentials = true;

            const _this = this;
          xhr.addEventListener("readystatechange", function() {
              if(this.readyState === 4) {
                    const resp = this.responseText

                    if (this.status == 401) {
                        _this.loginFailed = true
                        console.log(resp, _this.loginFailed)
                    }
              }
          });

          xhr.open("POST", `${config.API_URL}/login`);
          xhr.setRequestHeader("Content-Type", "application/json");

          xhr.send(data);
      }
  }
}
</script>

